const URL_CIRCLE = 'https://i.imgur.com/EpFWMQl.png';
const URL_MIDO = 'https://i.imgur.com/cNvWq7Z.png';

const URL = URL_MIDO;

export default function Product() {
	const [state_text, set_text] = React.useState<string>(TESTTEXTL);
	const [state_bgLocalFileBase64, set_bgLocalFileBase64] =
		React.useState<string>();
	const [state_bgUrl, set_bgUrl] = React.useState<string>();
	const installedFontFamilies: string[] = React.useMemo(
		() => flatJoin(',', filterInstalledFont(FONT_FAMILIES)),
		[],
	);
	const [state_fontFamily, set_fontFamily] = React.useState<string>(
		installedFontFamilies[0],
	);
	const [state_fontSize, set_fontSize] = React.useState<number>(28);
	const [state_fontColor, set_fontColor] = React.useState<string>(
		FONT_COLORS[0],
	);
	const fontStrokeColorNames: string[] = React.useMemo(
		() => FONT_STROKE_COLORS.map((e) => e.name),
		[],
	);
	const [state_fontStrokeColorName, set_fontStrokeColorName] =
		React.useState<string>(fontStrokeColorNames[0]);
	const [state_lineHeight, set_lineHeight] = React.useState<number>(1.5);
	const [state_origImageW, set_origImageW] = React.useState<number>(100);
	const [state_origImageH, set_origImageH] = React.useState<number>(100);
	const [state_maxStageW, set_maxStageW] = React.useState<number>();
	const ref_stage = React.useRef<Konva.Stage>(null);
	const ref_image = React.useRef<Konva.Image>(null);

	React.useEffect(() => {
		(async () => {
			await setOrigImageSize(state_bgLocalFileBase64 ?? state_bgUrl ?? '');
		})();
	}, [state_bgLocalFileBase64, state_bgUrl]);
	React.useEffect(() => {
		const r = document.getElementById('textFormContainer');
		r && set_maxStageW(r.offsetWidth);
	}, []);

	const onFontFamilyChange = React.useCallback(
		(event: SelectChangeEvent) => set_fontFamily(event.target.value),
		[],
	);

	const setOrigImageSize = async (url: string) => {
		try {
			const res = await loadImage(url);
			set_origImageW(res.width);
			set_origImageH(res.height);
			console.log(['hyou!', res.width, res.height]);
		} catch (e) {
			console.log('hyou! onload error', e);
		}
	};

	const onTextChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_text(e.target.value),
		[],
	);

	const onFonColorChange = React.useCallback(
		(e: ColorResult) => set_fontColor(e.hex),
		[],
	);

	/**
	 * 選択された文字縁色名から対応するカラーコードを選択してメモ化
	 */
	const fontStrokeColorValue = React.useMemo(
		() =>
			R.pipe(
				R.find<t_fontStrokeColor>(R.propEq('name', state_fontStrokeColorName)),
				R.defaultTo(FONT_STROKE_COLORS[0]),
				R.prop('value'),
			)(FONT_STROKE_COLORS),
		[state_fontStrokeColorName],
	);

	const onLineHeightChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_lineHeight(Number(e.target.value)),
		[],
	);

	const onMaxStageWChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_maxStageW(Number(e.target.value)),
		[],
	);

	const save = () => {
		const uri = ref_stage.current?.toDataURL();
		uri && downloadURI(uri, 'stage.png');
	};
	const onTest = () => {
		ref_image.current?.moveToTop();
	};

	return (
		<div>
			<Stack spacing={2}>
				<Container maxWidth='sm'>
					<Stack spacing={3}>
						<div id='textFormContainer'>
							<TextForm value={state_text} onChange={onTextChange} />
						</div>
						<BgImageSelectTabs
							url={state_bgUrl}
							onUrlChange={set_bgUrl}
							onFileImageLoaded={set_bgLocalFileBase64}
						/>
						<Divider />

						<FontFamilySelector
							fontFamilies={installedFontFamilies}
							value={state_fontFamily}
							onChange={onFontFamilyChange}
						/>
						<FontSizeSelector
							sizeList={FONT_SIZE_LIST}
							value={state_fontSize}
							onChange={set_fontSize}
						/>
						<div
							style={{
								display: 'flex',
								flexDirection: 'row',
								justifyContent: 'flex-start',
								alignItems: 'flex-end',
							}}
						>
							<p
								style={{
									marginLeft: 0,
									marginTop: 0,
									marginBottom: 0,
									marginRight: 8,
									padding: 0,
									color: 'rgba(0, 0, 0, 0.6)',
								}}
							>
								文字色:{' '}
							</p>
							<div style={{ flexGrow: 2 }}>
								<TwitterPicker
									onChange={onFonColorChange}
									color={state_fontColor}
									colors={FONT_COLORS}
									triangle={'hide'} //吹き出し
									width={'100%'}
								/>
							</div>
						</div>
						<FontStrokeColorSelector
							strokeColorNames={fontStrokeColorNames}
							onChange={set_fontStrokeColorName}
							value={state_fontStrokeColorName}
						/>
						<TextField
							id='line-height'
							label='line height'
							type='number'
							InputLabelProps={{
								shrink: true,
							}}
							size='small'
							value={state_lineHeight}
							onChange={onLineHeightChange}
							inputProps={{
								step: 0.1,
								min: 0,
								max: 99999,
								type: 'number',
							}}
						/>
						{state_maxStageW !== undefined && (
							<TextField
								id='max-width'
								label='max width'
								type='number'
								InputLabelProps={{
									shrink: true,
								}}
								size='small'
								value={state_maxStageW}
								onChange={onMaxStageWChange}
							/>
						)}
						<Demo />
						<Button variant='contained' onClick={onTest}>
							test
						</Button>
						<Button variant='contained' onClick={save}>
							save
						</Button>
					</Stack>
				</Container>
				{state_maxStageW !== undefined && (
					<div
						style={{
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'center',
						}}
					>
						<div
							style={{
								overflow: 'auto',
								maxWidth: '100%',
								maxHeight: '90vh',
							}}
						>
							<Canvas
								fontFamily={state_fontFamily}
								fontSize={state_fontSize}
								fontColor={state_fontColor}
								fontStrokeColor={fontStrokeColorValue}
								lineHeight={state_lineHeight}
								maxStageWidth={state_maxStageW}
								text={state_text}
								imageUrl={state_bgLocalFileBase64 ?? state_bgUrl ?? ''}
								origImageH={state_origImageH}
								origImageW={state_origImageW}
								ref_stage={ref_stage}
								ref_image={ref_image}
							/>
						</div>
					</div>
				)}
			</Stack>
		</div>
	);
}

const Demo = () => {
	const { width, height } = useWindowSize();

	return (
		<div>
			<div>width: {width}</div>
			<div>height: {height}</div>
		</div>
	);
};

/**
 * fontListの各要素の内1つででもデバイスが所有しているフォントだけを残すfilter
 * @param fontList
 * @returns
 */
const filterInstalledFont = (fontList: string[][]) =>
	R.filter(R.any(isFontInstalled), fontList);

const isFontInstalled = (fontName: string) =>
	document.fonts.check('bold 12px ' + fontName);
