const LOCAL_IMAGE_INPUT_ID = 'local-image';

const BgImageSelectTabs = React.memo(
	(props: {
		url?: string;
		onUrlChange: (e: string) => any;
		onFileImageLoaded: (e: string) => any;
	}) => {
		const [state_currentTabId, set_currentTabId] = React.useState('1');
		const handleChange = (
			event: React.SyntheticEvent<Element, Event>,
			value: any,
		) => set_currentTabId(value);

		const _onUrlChange = React.useCallback(
			(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
				props.onUrlChange(e.target.value),
			[],
		);

		return (
			<TabContext value={state_currentTabId}>
				<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
					<TabList
						value={state_currentTabId}
						variant='standard'
						onChange={handleChange}
					>
						<Tab label='背景画像URL' value='1' />
						<Tab label='背景画像をアップロード' value='2' />
						<Tab label='背景色' value='3' />
					</TabList>
				</Box>
				<Box
					style={{
						position: 'relative',
						marginTop: 16,
					}}
				>
					{/*高さを確保するために1つだけrelativeにする。 */}
					<TabPanel value='1' style={{ position: 'relative' }}>
						<TextField
							id='bgimage-url-input'
							label='画像URLを入力'
							variant='outlined'
							size='small'
							fullWidth
							value={props.url}
							onChange={_onUrlChange}
						/>
						<Tooltip
							title='URL入力補助コマンド: !aaa, !bbb, !ccc'
							placement='top'
						>
							<IconButton>
								<TipsAndUpdatesOutlined />
							</IconButton>
						</Tooltip>
					</TabPanel>
					<TabPanel value='2'>
						<Input onFileImageLoaded={props.onFileImageLoaded} />
					</TabPanel>
					<TabPanel value='3'>Item Three</TabPanel>
				</Box>
			</TabContext>
		);
	},
);
