const ImageSelector = (props: {
	localImageInputId: string;
	value: string;
	onChange: (
		e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>,
	) => any;
}) => {
	const on = React.useCallback(
		(e) => document.getElementById(props.localImageInputId)?.click(),
		[props.localImageInputId],
	);
	return (
		<TextField
			id='bgimage-input'
			label='Outlined'
			variant='outlined'
			size='small'
			fullWidth
			value={props.value}
			onChange={props.onChange}
		/>
	);
};

const Input = (props: { onFileImageLoaded: (url: string) => any }) => {
	React.useEffect(() => {
		console.log({
			tabbbb: document.getElementById(LOCAL_IMAGE_INPUT_ID),
		});

		document.getElementById(LOCAL_IMAGE_INPUT_ID)?.addEventListener(
			'change',
			function (e) {
				const r = R.tryCatch(
					R.pipe(
						getFileDataFromEvent,
						throwIfNone,
						(e) => (e.type.match('image.*') ? e : false),
						throwIfEquals<File>(false),
						(e) => readFileAsDataUrl(e, props.onFileImageLoaded),
					),
					R.always(false),
				)(e);
				if (r === false) {
					alert('画像を選択できませんでした。');
					return;
				}
			},
			false,
		);
	}, [props.onFileImageLoaded]);

	return <input id={LOCAL_IMAGE_INPUT_ID} type='file' accept='image/*' />;
};

export default BgImageSelectTabs;
